<template>
    <div>
        <el-table
            :data="appStatusHistoryTableList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="selectItem"
            @filter-change="filterStatus"
            :default-sort = "{prop: 'ts', order: 'descending'}"
            ref="appStatusHistoryTable"
             v-loading="listLoading" 
            >
            <el-table-column
            type="selection"
            min-width="50"
            :selectable="checkSelectable">
            </el-table-column>

            <el-table-column
            prop="devname"
            :label="$t('appSecurity.deviceName')"
            min-width="120"
            >
            </el-table-column>

            <el-table-column
            prop="taskname"
            :label="$t('appSecurity.taskName')"
            min-width="120"
            >
            </el-table-column>

            <el-table-column
            prop="filename"
            :label="$t('appSecurity.apkName')"
            min-width="140"
            >
            </el-table-column>

            <el-table-column
            :label="$t('appSecurity.funName')"
            min-width="130"
            prop="target"
                >
                <template slot-scope="scope">
                    {{scope.row.target|appStatusFunction}}
                </template>
            </el-table-column>
            <el-table-column
            :label="$t('appSecurity.time')"
            min-width="130"
            prop="ts"
                >
                <template slot-scope="scope">
                    {{scope.row.ts|time}}
                </template>
            </el-table-column>

            <el-table-column
            column-key="status"
            prop="status"
            :label="$t('appSecurity.installStatus')"
            min-width="130"
            :filters="[{ text: $t('global.success'), value: 2 }, { text: $t('global.failed'), value: 1 }, {text: $t('global.running'), value: 0}]"
            filter-placement="bottom-end"
            >
                <template slot-scope="scope">
                    <img src="@/assets/imgs/loading1.gif" alt="" style="width:50px;height:10px;"v-if="scope.row.status==0">
                    <span v-if="scope.row.status==2" class="badge bg-green">{{$t('global.success')}}</span>
                    <span v-if="scope.row.status==1" class="badge bg-DimGray">{{$t('global.failed')}}</span>
                    <!-- {{scope.row.upstatus|AppStatus}} -->
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('appSecurity.errorStatus')"
            min-width="100"
            >
                <template slot-scope="scope">
                    {{(""+scope.row.errorcode)|appErrorStatus}}
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('appSecurity.action')"
            min-width="175"
            align="center">
                <template slot-scope="scope">
                    <div>
                        <el-button-group>
                            <el-button 
                                size="mini" 
                                type="danger"
                                @click="deleteHistory(scope.row.aisid)"
                                :disabled="scope.row.status==0"
                                >
                                    {{$t('global.delete')}}
                            </el-button>
                            <el-button 
                                size="mini" 
                                type="primary"
                                @click="reexec(scope.row)"
                                :disabled="scope.row.status!=1"
                                >
                                    {{$t('appSecurity.reexec')}}
                            </el-button>
                        </el-button-group>
                    </div>
                </template>
            </el-table-column>

        </el-table>

        <btn-group 
        :selectedData="multipleSelection" 
        :isDelete="true" 
        :isMove="false" 
        tid="aisid" 
        :delApi="batchDeleteAppStatusHistory" 
        @result="isSuccess" 
        v-show="isShowBtnGroup"
        class="fl m-t-10 cf">
        </btn-group>

        <el-button 
        v-loading="clearLoading" 
        size="small" 
        @click="clearAll()" 
        type="primary" 
        class="fl m-t-10 cf m-l-10"
            v-show="isShowBtnGroup">
        {{$t('appSecurity.clearAll')}}
        </el-button>

        <div class="m-t-10 cf fr">
            <el-pagination
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :page-size="limit"
            :current-page="currentPage"
            :total="dataCount"
            v-show="isshow"
            background
            >
            </el-pagination>
            
        </div> 
        
       
    </div>
</template>
<script>
    import {appControl} from '@/assets/js/lwm2mMap'
    import {reExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import{getAppStatusByTypeApi, 
    batchDeleteAppStatusHistoryApi,
    deleteAppStatusHistoryAllApi,
    deleteAppStatusHistoryAllByTypeApi, 
    deleteAppStatusHistoryApi, 
    getAppStatusOverviewApi,
    getAppStatusByTypeAndDevIdApi, 
    getAppStatusByTypeAndGroupApi,
    getAppStatusByTypeAndPageInGroupApi,
    getAppStatusByTypeAndPageAndStatusInGroupApi,
    deleteAppStatusHistoryAllByGroupAndTypeApi} from "@/components/restfulapi/appStatusApi"
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"
    import handleResponse from '@/components/restfulapi/handleResponse'
    import btnGroup from "../../../common/btn-group";
    import {batchWlType} from "../../../assets/js/constant"

export default {
    
    data() {
        return {
            appStatusHistoryTableList: [],
            appStatusHistoryTableData: [],
            isshow: false,
            limit: 10,
            dataCount: null,
            currentPage: 1,
            multipleSelection: [],
            isShowBtnGroup: false,
            clearLoading: false,
            batchDeleteAppStatusHistory:  batchDeleteAppStatusHistoryApi,
            intervalTime: 3000,

            batchWlType,
            listLoading: false,
            isInterval: false,
            statusArray: []
        }
    },

    components: {
        btnGroup,
    },

    props: {
        statusType: {
            default: batchWlType,
            type: String
        },

        selectedAgentId: {
            default: "",
            type: String
        },

        groupname: {
            default: "",
            type: String
        }
    },
    methods: {
        selectItem(val) {
            this.multipleSelection = val;
        },

        checkSelectable(row) {
            return !row.status==0;
        },

        handleCurrentChange(currentPage){
           if(currentPage){
                this.currentPage = currentPage;
            }else{
                this.currentPage = 1
            }
            this.intervalGetAppStatusHistory();
           
        },

        deleteHistory(aisid){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.listLoading = true;
                    deleteAppStatusHistoryApi(aisid).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetAppStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            }) 
        },

        getAppStatusHistoryByGroupname(){
            if(!this.groupname){
                console.error("[getAppStatusHistoryByGroupname]groupname is empty")
            }
            if(this.isInterval === false){
                this.listLoading = true;
            }
            if(this.statusArray.length > 0){
                getAppStatusByTypeAndPageAndStatusInGroupApi(this.groupname, this.statusType, this.currentPage, this.limit, this.statusArray).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignAppTableData(res);
                    })
                })
            }else{
                getAppStatusByTypeAndPageInGroupApi(this.groupname, this.statusType, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignAppTableData(res);
                    })
                })
            }
            
        },


        assignAppTableData(res){
            if(Object.prototype.toString.call(res)  === '[object Object]'&& Object.prototype.toString.call(res.data) === '[object Array]'){
                this.dataCount = res.count;
                this.appStatusHistoryTableList = res.data
                this.isshow = this.dataCount > this.limit;
                this.isShowBtnGroup = this.dataCount > 0;
                let isFinish = true;
                this.$emit("getStatusCount")
                res.data.forEach((val) => {
                    if(val.status == 0){
                        isFinish = false;
                    }
                })
                if(isFinish){
                    this.$emit("isFinished");
                    window.clearInterval(_g.timer);
                    _g.timer = null;
                }
            }else{
                window.clearInterval(_g.timer);
                _g.timer = null;
            }
        },

        intervalGetAppStatusHistory(){
            this.getAppStatusHistoryByGroupname();
            if(_g.timer === null){
                _g.timer = window.setInterval(() => {
                    this.getAppStatusHistoryByGroupname();
                    this.isInterval = true;
                },this.intervalTime)
            }
        },


        filterStatus(value){
            this.statusArray = value.status;
            this.currentPage = 1;
            if(_g.isArray(this.statusArray)){
                this.intervalGetAppStatusHistory();
            }
        },

        checkAppStatusData(){
            getAppStatusByTypeAndGroupApi(this.groupname, this.statusType)
        },

        resetAppSensor(sensorId, selectedAgentId, appdata){

            this.listLoading = true;
            reExecDeviceStatus(selectedAgentId, sensorId, appdata).then((data) => {
                this.listLoading = false;
                handleResponse(data, (res) => {
                    if(res.status == "CHANGED"){
                        this.activeName = "appStatusHistory";
                        this.currentPage = 1;
                        this.intervalGetAppStatusHistory();
                    }else{
                        _g.handleError(res)
                    }
                })
                
            })
            
        },

        reexec(appItem){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('appSecurity.reexec'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    let appdata = {
                        taskname: appItem.taskname,
                        filename: appItem.filename,
                        pkgname: appItem.pkgname,
                        versioncode: appItem.versioncode,
                        version: appItem.versionname,
                    }
                    this.resetAppSensor(appItem.target, appItem.deviceId, appdata);
                    
                }
            })
        },

        //delete appstatus $emit
        isSuccess(result){
            if(result){
                this.intervalGetAppStatusHistory();
            }
        },

         //clear All history
        clearAll(){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.clearLoading = true;
                    deleteAppStatusHistoryAllByGroupAndTypeApi(this.groupname, this.statusType).then((data) => {
                        this.clearLoading = false;
                        handleResponse((data), (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetAppStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            })
        },

        initData(){
            this.appStatusHistoryTableList = [];
            this.isShowBtnGroup= false;
            this.isshow= false;
            this.currentPage = 1;
        }


    },

    created(){
        this.$emit("getStatusCount") 
    },
    beforeDestroy() {
         window.clearInterval(_g.timer);
        _g.timer = null;
    },

    watch: {
        groupname(val){
            if(val){
                this.initData();
                this.checkAppStatusData();
                this.intervalGetAppStatusHistory();
                
            }  
},

    },
        
}
</script>
<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        // background-color: #777;
        border-radius: 10px;
    }

    .el-loading-mask{
        top: 60px;
    }

</style>



