import {apiGet, apiPut, apiDelete, apiPost} from '../../assets/js/baseApi'


let getExecStatusApi = function(){
    return new Promise((resolve, reject) => {
        apiGet("/api/execstatusmgr/status").then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}
let getExecStatusByTypeApi = function(type){
    return new Promise((resolve, reject) => {
        let data = {
            type
        }
        apiGet("/api/execstatusmgr/bytype", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

