let batchDeleteExecStatusHistoryApi = function(aeids){
    let aeidsJson = JSON.stringify(aeids);
    let data = {
        aeids: aeidsJson
    }
    let config = {
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        },
    };
    return new Promise((resolve, reject) => {
        apiPost("/api/execstatusmgr/batchdelete", data, config).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getExecStatusOverviewApi = function(taskname, type){
    let data={
        taskname,
        type
    }

    return new Promise((resolve, reject) => {
        apiGet("/api/execstatusmgr/overview", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

export {
    getExecStatusApi,
    deleteExecStatusHistoryApi,
    batchDeleteExecStatusHistoryApi,
    getExecStatusOverviewApi,
    deleteExecStatusHistoryAllApi,
    getExecStatusByTypeApi,
    getExecStatusByTypeAndDevIdApi, 
    getExecStatusByTypeAndGroupApi,
    deleteExecStatusHistoryAllByTypeApi,
    getExecStatusByPageAndTypeAndStatusInGroupApi,
    getExecStatusByPageAndTypeInGroupApi,
    deleteExecStatusHistoryAllByDevIDAndTypeApi,
    deleteExecStatusHistoryAllByGroupAndTypeApi
}


