<template>
    <div class="content-box">
        <div class="title-box">
            <p class="header-line">
                <svg t="1569376934286" class="icon m-r-10" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2530" width="18" height="18">
                    <path d="M718.188013 520.572236H305.821197v-51.54598c0-18.978237 15.385409-34.363645 34.363646-34.363645h343.639524c18.978237 0 34.363645 15.385409 34.363646 34.363645v51.54598z" :fill="appSecurityIconColor" p-id="2531"></path>
                    <path d="M512 958.708971c-13.909801 0-27.836999-4.202719-39.71656-12.618388L296.452829 821.496913C191.213973 738.90894 133.995807 622.150726 133.995807 491.733421V255.221776a34.370809 34.370809 0 0 1 19.648502-31.05018L473.626018 72.453149a31.991622 31.991622 0 0 1 3.691065-1.493004c22.299889-7.550976 46.914496-7.567349 69.248154 0 1.258667 0.419556 2.499937 0.923022 3.691065 1.493004l320.098366 151.718447a34.362622 34.362622 0 0 1 19.648502 31.05018v236.512668c0 130.433678-57.217143 247.183705-161.098072 328.756559L551.732933 946.082396c-11.879562 8.41567-25.806759 12.626575-39.732933 12.626575zM202.724121 276.958846v214.774575c0 108.872616 47.88766 206.427466 134.821615 274.701432l174.454264 123.571387 175.813215-124.586507c85.591377-67.251683 133.462664-164.80551 133.462664-273.686312V276.958846L522.906397 135.543046c-7.131421-2.064009-14.832823-2.064009-21.930475 0L202.724121 276.958846z" :fill="appSecurityIconColor"  p-id="2532"></path>
                    <path d="M512 676.532287c-18.977213 0-34.363645-15.386432-34.363645-34.363645V313.068252c0-18.977213 15.386432-34.363645 34.363645-34.363646s34.363645 15.386432 34.363645 34.363646V642.168642c0 18.977213-15.386432 34.363645-34.363645 34.363645z" :fill="appSecurityIconColor" p-id="2533"></path>
                </svg>
                {{$t('appSecurity.appSecurity')}}
            
            </p>
        </div>
        
        <div class="select-box">
            <mode-switch @mode-status="changeMode" ></mode-switch>
            <span class="vertical"></span>
            <select-group 
            @select-getDeviceOptions="getGroupOption" 
            @select-getGroupName="getGroupName" 
            :isSingleMode="isSingleMode"
            :groupType="groupTypeArray">
            </select-group>
            <select-device 
            @select-device="getDeviceOption"
            @isOfflineDev="isOfflineDev" 
            :isSingleMode="isSingleMode"
            :isDisabledOffDev="isDisabledOffDev"
            :groupType="groupType">
            </select-device>
        </div>
        <div class="table-box" >
            <single-control 
            :selectedAgentId="selectedAgentId" 
            :isSingleMode="isSingleMode"
            @isDisabled="isDisabled"
            :isOffline="isOffline">
            </single-control>
            <batch-control 
            :selectedAgentsData="selectedAgentsData" 
            :isSingleMode="isSingleMode"
            :groupname="groupname"
            ></batch-control>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import "../../assets/css/colors"; 
    
</style>
<script>
    import selectDevice from '../../common/select-device'
    import modeSwitch from "../../common/mode-switch"
    import selectGroup from "../../common/select-group"
    import singleControl from "./appsecuritydetails/single"
    import batchControl from "./appsecuritydetails/batch"
    import {groupType} from "../../assets/js/constant"

    export default{
        name: "appSecurity",
        data(){
            return {
                selectedAgentId: '',
                selectedAgentsData: [],
                isSingleMode: true,
                groupname: '',
                //offline device is disabled
                isDisabledOffDev: true,
                //selectedAgent is offline
                isOffline: false,
                appSecurityIconColor: "#303133",
                groupType: groupType.android,
                groupTypeArray: [groupType.android]
            }
        },
        components:{
            selectDevice,
            selectGroup,
            modeSwitch,
            singleControl,
            batchControl
        },
        methods:{

            getDeviceOption(selectedAgentId){ 
                this.selectedAgentId = selectedAgentId;
            },

            getGroupOption(selectedAgentsData){
                this.selectedAgentsData = selectedAgentsData;
            },

            initData(){
                this.selectedAgentId = "";
                this.groupname = "";
                this.selectedAgentsData = [];
            },

            changeMode(val){
                this.initData();
                this.isSingleMode = !val
            },

            getGroupName(groupname){
                this.groupname = groupname;
            },

            isDisabled(value){
                this.isDisabledOffDev = value;
            },
            isOfflineDev(value){
                this.isOffline = value
            }
            
        },

    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/colors";
    .icon{
        position: relative;
        top: 3px;
    }
           
</style>






