<template>
    <el-tabs v-model="activeName" v-show="isSingleMode">
        <el-tab-pane name="whitelist" v-loading="whitelistLoading">
            <span slot="label">
                <i class="fa fa-sliders m-r-5" aria-hidden="true"></i>
                {{$t('appSecurity.whitelistManagement')}}
            </span>

            <div class="m-b-20 m-r-15 cf" v-show="whitelist">      
                <el-button 
                    size="small" 
                    type="warning"
                    class="fr"
                    @click="statusChange(false, 'whitelist')"
                    >
                    <i class="fa fa-stop m-r-5" aria-hidden="true"></i>
                        {{$t('appSecurity.disable')}}
