                    </svg>
                </p>
               
                <el-button  
                    size="small"
                    type="success"
                    @click="statusChange(true, 'whitelist')"
                    >
                    <i class="fa fa-play m-r-5" aria-hidden="true"></i>
                        {{$t('appSecurity.enable')}}
                </el-button>
            </div>  
            <div class="text-center noData" v-show="!appTypeShow">
                <p>
                    <img src="@/assets/imgs/noData.png" alt="" style="">
                    {{$t('appSecurity.noData')}}
                </p>
            </div>    
            <card-temp
