                        <path d="M721.856 946.624l-512.704-0.256a94.4 94.4 0 0 1-93.824-94.848V171.328c0-52.288 42.048-94.848 93.824-94.848h543.68c51.84 0 93.952 42.56 93.952 94.848v209.024a30.72 30.72 0 0 0 0.128 4.224l0.064 0.576a37.184 37.184 0 0 0 55.296 27.584 37.12 37.12 0 0 0 17.92-27.584l0.64-243.84C920.832 63.424 858.112 0 780.928 0H181.184C104 0 41.216 63.424 41.216 141.312v741.44C41.216 960.704 104 1024 181.184 1024h540.672c21.888 0 39.68-17.344 39.68-38.656s-17.792-38.72-39.68-38.72z" p-id="3850" fill="#aaaaaa"></path>
