                        <svg t="1572233566165" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                            p-id="10804" width="20" height="20"
                            v-else
                            @click="handleWhitelist(props.item.package, 'removeWhitelist')" >
                            <path d="M744.512 392.768L453.825 681.984l-41.92-41.664 290.689-289.28 41.92 41.728z m-423.168 73.984l132.481 131.84-41.92 41.728-132.481-131.904 41.92-41.664z m527.872 479.872H174.785c-62.016 0-112.384-48.704-112.384-108.671V186.05c0-59.968 50.432-108.671 112.384-108.671h674.431c62.016 0 112.384 48.704 112.384 108.671v651.903c0 59.968-50.368 108.671-112.384 108.671z m56.192-760.576c0-29.952-25.217-54.336-56.193-54.336H174.784c-30.976 0-56.193 24.384-56.193 54.336v651.903c0 29.952 25.217 54.336 56.193 54.336h674.431c30.976 0 56.193-24.384 56.193-54.336V186.048z" p-id="10805" fill="#409EFF"></path>
                        </svg>
                    </p>
                </div>
                <img :src="getSrc(props.item.raid)" :onerror="defaultImg" alt="no img" slot="card-body-img" slot-scope="props"/>
            </card-temp> 
        </el-tab-pane>
    </el-tabs>
</template>
<script>
    import {appControl} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus } from '@/components/restfulapi/deviceStatusApi';
    import {androidControl } from '@/assets/js/lwm2mMap';
    import {getRepoAppsApi, getRepoApi} from '@/components/restfulapi/getRepoAppApi';
    import {getSession, checkSession, setSession } from '../../../assets/js/storage';
    import {getRepoSrcApi} from "../../restfulapi/serverConfigApi"
    import {singleInstallType, batchInstallType, disableAppArray} from "../../../assets/js/constant";
    import appHistory from "./history"
    import cardTemp from "../../../common/cardTemp"

    export default{
        name: 'controlAppcontrol',
        data(){
            return {
                activeName: "whitelist",
                appTableData: [],

                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                //history
                singleInstallType,
                //card
                appType: "uninstallApp",
                //whitelist
                whitelist: false ,  
                whitelistLoading: false, 

            }
        },
        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },
        components: {
            appHistory,
 	        cardTemp
        },
        methods:{
            getSensorStatus(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }
                let deviceAppArray = [];
                this.whitelistLoading = true;
                getDeviceStatus(this.selectedAgentId, appControl["getThirdAppInfo"]).then((obj) => {
                    this.whitelistLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.content.value){
                            let apppackageinfo = JSON.parse(res.content.value);
                            let allAppData = apppackageinfo.data;
                            if(Object.prototype.toString.call(allAppData) === "[object Array]" && allAppData.length > 0){
                                allAppData.forEach((val) => {
                                    //the kioskApp not operation whitelist
                                    if(disableAppArray.indexOf(val.packageName) < 0&& val.isKioskApp != 'true'){
                                        let uninstallAppObj = {
                                            type: 'uninstallApp',
                                            appname: val.appName,
                                            package: val.packageName,
                                            versioncode: val.versionCode,
                                            version: val.versionName,
                                            inwhitelist: val.inWhiteList,
                                            address: "",
                                            raid: "",
                                        };
                                        deviceAppArray.push(uninstallAppObj);
                                    }
                                        
                                })
                            }
                        }
                        
                        this.getRepoApps(deviceAppArray);  
                          
                    })   
                })
            },
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_apk_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.package === rem_apk_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
                
            },
            //get repo apps
            getRepoApps(deviceAppArray){
                if(Object.prototype.toString.call(deviceAppArray) != "[object Array]"){
                    console.error("getRepoAppsparameter is not array")
                    return;
                }
                this.whitelistLoading = true;
                getRepoAppsApi().then((data) => {
                    this.whitelistLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    deviceAppArray.forEach((deviceapp_val, index) => {
                                        if(val.pkgname === deviceapp_val.package){
                                            // update address in deviceapp to show icon
                                            deviceAppArray[index].address = val.address;
                                            deviceAppArray[index].raid = val.raid;
                                            
                                        }
                                    })
                                   
                                })
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            
                        }
                        this.appTableData = deviceAppArray;
                    })
                    
                })
            },

            
            getSrc(raid){
                if(raid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                    return 'static/imgs/default_apk.png';
                }
            },

            initData(){
                this.whitelist = false;
                this.appTableData = [];
            },
            //add and remove whitelist
            handleWhitelist(pkgname, cid){
                this.cid = cid;
                this.whitelistLoading = true;
                 setDeviceStatus(this.selectedAgentId, appControl[cid], pkgname).then((data) => {
                     this.whitelistLoading = false;
                    handleResponse(data, (res) =>{
                        if(res.status === "CHANGED"){
                            this.getSensorStatus();
                        }else{
                            _g.handleError(res);
                        }
                    })
                    
                })
            },

            //get whitelist server status
            getServerStatus(){
                let sensor_key = "whitelist"
                getDeviceStatus(this.selectedAgentId, androidControl[sensor_key]).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let status = res.content.value;
                            if(typeof(status) === "boolean"){
                                this[sensor_key] = status;
                            }else if(status === "true" || status === "false"){
                                this[sensor_key] = status === "true"? true: false;
                            }else{
                                this[sensor_key] = status;
                            }
                        }else{
                            _g.handleError(res)
                        }  
                    })
                })
                
            },
            //set whitelist server status
            statusChange(setSensorVal, command){
                let cid = command;
                let setSensorId = androidControl[cid];
                 if(setSensorId === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                _g.openGlobalLoading();
                setDeviceStatus(this.selectedAgentId, setSensorId, setSensorVal).then((data) => {
                    handleResponse(data, (res) =>{
                        if(res.status === "CHANGED"){
                            this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                this.getServerStatus();
                                this.getSensorStatus();
                            })
                        }else{
                            _g.handleError(res);
                        }
                    })
                    
                })
            },
            
        },
        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },
            selectedAgentId(val){
                if(this.isSingleMode) {
                    this.initData();

                    if(val&& !this.isOffline){
                        this.getSensorStatus();
                        this.getServerStatus();
                        // this.$refs.appHistory.intervalGetAppStatusHistory();
                    }
                }
            },
           
        },
        computed: {
            cardAppData(){
                return this.appTableData;
            },
            appTypeShow(){
                if(this.cardAppData.length >0){
                    return true;
                }else{
                    return false;
                }
                
            },

            subIntro(){
                return ['appname', 'version'];
            },
            appTypeArray(){
                return ['uninstallApp'];
            },

        }
    }
</script>

<style lang="scss" scoped>
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }
    .inwhite-icon{
        display: inline-block;
        position: relative;
        top:3px;
    }

    .action-icon{
        display: inline-block;
        cursor: pointer;
        position: relative;
        top:3px;
    }
</style>



