            top:3px;
        }
    }
</style>

<script>
    import {appControl, androidControl} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {batchWlType, disableAppArray} from "@/assets/js/constant"
    import {getRepoAppsApi, getRepoApi} from '@/components/restfulapi/getRepoAppApi'
    import {getExecStatusOverviewApi} from "@/components/restfulapi/execStatusApi";
    import {getAppStatusOverviewApi} from "@/components/restfulapi/appStatusApi"
    import {getDeviceStatus, setDeviceStatus, batchExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"
    import {getRepoSrcApi} from "../../restfulapi/serverConfigApi"

    import appHistory from "./history"
    import execHistory from "./execHistory"
    import cardTemp from "../../../common/cardTemp"

    export default{
        name: 'Appcontrol',
        data(){
            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "whitelist",
                appTableData: [],

                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                dialogTasknameVisible: false,
                //wl exec
                filename: '',
                pkgname: '',
                version: '',
                versioncode: '',
                //batch installapp
                wlForm:{
                    taskname: '',
                },
                rules:{
                    taskname: {required: true, validator: validateTaskname, trigger: 'blur'}
                },
                cid: "",
                dialogTasknameVisible: false,
                multipleSelection: [],
                batchWlType: batchWlType,
                //whitelist
                whitelist: false,  
                setWlVal: "",

                whitelistLoading: false,

                //installstatus count show
                isShowOverview: false,
                local_taskname: '',

                success: 0,
                installing: 0,
                failed: 0,
                //exec count show
                isExecShowOverview: false,
                local_exec_taskname: '',

                exec_success: 0,
                exec_installing: 0,
                exec_failed: 0,
            }
        },

        props:{
            selectedAgentsData: {
                type: Array,
                default: []
            },
            groupname: {
                type: String,
                default: ""
            }, 
            isSingleMode: {
                type: Boolean,
                default: true
            }
        },

        components: {
            appHistory,
            cardTemp,
            execHistory
        },

        methods:{
            getSensorStatus(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                if(this.selectedAgentsData.length == 0){
                    console.error("No device is online");
                    return;
                }
                let deviceAppArray = [];
                this.whitelistLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, appControl["getThirdAppInfo"]).then((obj) => {
                    this.whitelistLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.content.value){
                            let apppackageinfo = JSON.parse(res.content.value);
                            let allAppData = apppackageinfo.data;
                            if(Object.prototype.toString.call(allAppData) === "[object Array]" && allAppData.length > 0){
                                allAppData.forEach((val) => {
                                    //the kioskApp not operation whitelist
                                    if(disableAppArray.indexOf(val.packageName) < 0&& val.isKioskApp != 'true'){
                                        let uninstallAppObj = {
                                            type: 'uninstallApp',
                                            appname: val.appName,
                                            package: val.packageName,
                                            versioncode: val.versionCode,
                                            version: val.versionName,
                                            inwhitelist: val.inWhiteList,
                                            address: "",
                                            raid: ""
                                        };
                                        deviceAppArray.push(uninstallAppObj);
                                    }
                                        
                                })
                            }
                        }
                        this.getRepoApps(deviceAppArray);    
                    })   
                })
            },
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_apk_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.package === rem_apk_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
                
            },
            //get repo apps for get address of app icon
            getRepoApps(deviceAppArray){
                if(Object.prototype.toString.call(deviceAppArray) != "[object Array]"){
                    console.error("getRepoAppsparameter is not array")
                    return;
                }
                this.whitelistLoading = true;
                getRepoAppsApi().then((data) => {
                    this.whitelistLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    deviceAppArray.forEach((deviceapp_val, index) => {
                                        if(val.pkgname === deviceapp_val.package){
                                            // update address in deviceapp to show icon
                                            deviceAppArray[index].address = val.address;
                                            deviceAppArray[index].raid = val.raid;
                                        }
                                    })
                                })
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            
                        }
                        this.appTableData = deviceAppArray;
                    })
                    
                })
            },

            
            getSrc(raid){
                if(raid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                    return 'static/imgs/default_apk.png';
                }
                
            },

	
            //batch install app
            batchExecWhitelist(){
                let cid = this.cid;
                if(cid == ""){
                    console.error("cid is empty");
                    return;
                }
               

                if(this.selectedAgentsData.length === 0){
                    this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                this.$refs.wlForm.validate((valid) => {
                    if(valid) {
                        _g.openGlobalLoading();
                        let appdata = "", sensorId = "";
                        if(cid == "whitelist"){
                            if(androidControl[cid] === undefined){
                                this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                                return;
                            }
                            sensorId = androidControl[cid];
                            appdata = {
                                groupname: this.groupname,
                                taskname: this.wlForm.taskname,
                                value: this.setWlVal
                            }
                        }else{
                            if(appControl[cid] === undefined){
                                this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                                return;
                            }
                            sensorId = appControl[cid];
                            appdata = {
                                groupname: this.groupname,
                                taskname: this.wlForm.taskname,
                                filename: this.filename,
                                pkgname: this.pkgname,
                                version: this.version,
                                versioncode: this.versioncode,
                            }
                        }
                        let taskname = this.wlForm.taskname;
                        
                        this.dialogTasknameVisible = false;
                        this.whitelistLoading = true;
                        batchExecDeviceStatus(sensorId, appdata).then((data) => {
                            this.whitelistLoading = false;
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    
                                    if(cid == "whitelist"){
                                        this.getSensorStatus();
                                        this.getServerStatus();
                                        setLocal("local_exec_tname", taskname);
                                        this.activeName = "execStatusHistory";
                                        this.$refs.execHistory.intervalGetExecStatusHistory();
                                    }else{
                                        setLocal("local_wl_tname", taskname);
                                        this.activeName = "wlStatusHistory";
                                        this.$refs.wlHistory.intervalGetAppStatusHistory();
                                    }
                                    
                                }else{
                                    _g.handleError(res);
                                }
                            })
                            
                        })            
                    }
                 })
                          
            },

            handleWhitelist(appinfo, cid){
                this.cid = cid;
                if(appControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }

                if(this.selectedAgentsData.length === 0){
                    this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                this.pkgname = appinfo.package;
                this.filename = appinfo.appname;
                this.version = appinfo.version;
                this.versioncode = appinfo.versioncode;
                this.wlForm.taskname = "";
                this.dialogTasknameVisible = true;
            },

            initApkInfo(){
                this.cid = "";
                this.filename = "";
                this.pkgname = "";
                this.version = "";
                this.wlForm.taskname = "";
                this.setWlVal = "";

            },

             //get updatedp status count
            getBatchAppStatusCount(){
                let local_taskname = getLocal("local_wl_tname");
                if(!local_taskname){
                    console.error("getBatchAppStatusCount:localStorage(local_wl_tname) is null")
                    this.isShowOverview = false;
                    return;
                }
                getAppStatusOverviewApi(local_taskname, batchWlType).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.local_taskname = local_taskname;

                            this.installing = res.installing;
                            this.failed = res.fail;
                            this.success = res.success;
                            if(this.total > 0 &&  this.activeName === "wlStatusHistory"){
                                this.isShowOverview = true;
                            }else{
                                this.isShowOverview = false;
                            }
                        }
                    })
                })
            },

            getBatchExecStatusCount(){
                let local_taskname = getLocal("local_exec_tname");
                if(!local_taskname){
                    console.error("getBatchExecStatusCount:localStorage(local_exec_tname) is null")
                    this.isShowOverview = false;
                    return;
                }
                getExecStatusOverviewApi(local_taskname, batchWlType).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.local_exec_taskname = local_taskname;

                            this.exec_installing = res.execing;
                            this.exec_failed = res.fail;
                            this.exec_success = res.success;
                            if(this.exec_total > 0 &&  this.activeName === "execStatusHistory"){
                                this.isExecShowOverview = true;
                            }else{
                                this.isExecShowOverview = false;
                            }
                        }
                    })
                })
            },
            
            initData(){
                this.whitelist = false;
                this.appTableData = [];
            },

            handleClick(tab){
                if(tab.name === "wlStatusHistory"){
                    this.getBatchAppStatusCount();
                }else{
                    this.isShowOverview = false;
                }

                if(tab.name === "execStatusHistory"){
                    this.getBatchExecStatusCount();
                }else{
                    this.isExecShowOverview = false;
                }
                
            },

            // whitelist server 
            getServerStatus(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                if(this.selectedAgentsData.length == 0){
                    console.error("No device is online");
                    return;
                }
                let sensor_key = "whitelist"
                getDeviceStatus(this.selectedAgentsData[0].value, androidControl[sensor_key]).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let status = res.content.value;
                            if(typeof(status) === "boolean"){
                                this[sensor_key] = status;
                            }else if(status === "true" || status === "false"){
                                this[sensor_key] = status === "true"? true: false;
                            }else{
                                this[sensor_key] = status;
                            }
                        }
                    })
                }) 
            },
            // change whitelist status
            statusChange(setWlVal, command){
                this.cid = command;
                this.setWlVal = setWlVal;
                let cid = command;
                if(androidControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')}).then(() => {
                        this[cid] = this.$options.data()[cid];
                    });
                    return false;
                }
                if(this.selectedAgentsData.length == 0){
                     this.$swal("",this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')}).then(() => {
                        this[cid] = this.$options.data()[cid];
                    })
                    return false;
                }

                this.wlForm.taskname = "";
                this.dialogTasknameVisible = true;
            },

            isFinished(){
                this.getSensorStatus();
            },
            isExecFinished(){
                this.getServerStatus();
            }
            
        },
       

       
        watch: {
            isSingleMode(val){
                if(!val){
                    this.initData();
                }
            },
            groupname(val){
                if(!this.isSingleMode&& val){
                    this.initData();
                    this.getSensorStatus();
                    this.getServerStatus();
                }
            }
        },

        computed: {
            cardAppData(){
                return this.appTableData;
            },
            appTypeShow(){
                if(this.cardAppData.length >0){
                    return true;
                }else{
                    return false;
                }
                
            },

            subIntro(){
                return ['appname', 'version'];
            },

            appTypeArray(){
                return ['uninstallApp'];
            },

            total(){
                return  this.success+ this.installing+ this.failed;
            },

            exec_total(){
                return this.exec_success+ this.exec_installing+ this.exec_failed;
            }
        }

    }
</script>




