<template>
    <div>
        <el-table
            :data="execStatusHistoryTableList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="selectItem"
            @filter-change="filterStatus"
            :default-sort = "{prop: 'ts', order: 'descending'}"
            ref="execStatusHistoryTable"
             v-loading="listLoading" 
            >
            <el-table-column
            type="selection"
            min-width="50"
            :selectable="checkSelectable">
            </el-table-column>

            <el-table-column
            prop="devname"
            :label="$t('appSecurity.deviceName')"
            min-width="120"
            >
            </el-table-column>

            <el-table-column
            prop="taskname"
            :label="$t('appSecurity.taskName')"
            min-width="120"
            >
            </el-table-column>
            <el-table-column
            prop="value"
            :label="$t('appSecurity.value')"
            min-width="120"
            >
                <template slot-scope="scope">
                    {{scope.row.value|execStatusFunction}}
                </template>
            </el-table-column>
            
            <el-table-column
            :label="$t('appSecurity.time')"
            min-width="130"
            prop="ts"
                >
                <template slot-scope="scope">
                    {{scope.row.ts|time}}
                </template>
            </el-table-column>

            <el-table-column
            column-key="status"
            prop="status"
            :label="$t('appSecurity.installStatus')"
            min-width="140"
            :filters="[{ text: $t('global.success'), value: 2 }, { text: $t('global.failed'), value: 1 }, {text: $t('global.running'), value: 0}]"
            filter-placement="bottom-end"
            >
                <template slot-scope="scope">
                    <img src="@/assets/imgs/loading1.gif" alt="" style="width:50px;height:10px;"v-if="scope.row.status==0">
                    <span v-if="scope.row.status==2" class="badge bg-green">{{$t('global.success')}}</span>
                    <span v-if="scope.row.status==1" class="badge bg-DimGray">{{$t('global.failed')}}</span>
                    <!-- {{scope.row.upstatus|ExecStatus}} -->
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('appSecurity.errorStatus')"
            min-width="100"
            >
                <template slot-scope="scope">
                    {{(""+scope.row.errorcode)|appErrorStatus}}
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('appSecurity.action')"
            min-width="175"
            align="center">
                <template slot-scope="scope">
                    <div>
                        <el-button-group>
                            <el-button 
                                size="mini" 
                                type="danger"
                                @click="deleteHistory(scope.row.aeid)"
                                :disabled="scope.row.status==0"
                                >
                                    {{$t('global.delete')}}
                            </el-button>
                            <el-button 
                                size="mini" 
                                type="primary"
                                @click="reexec(scope.row)"
                                :disabled="scope.row.status!=1"
                                >
                                    {{$t('appSecurity.reexec')}}
                            </el-button>
                        </el-button-group>
                    </div>
                </template>
            </el-table-column>

        </el-table>

        <btn-group 
        :selectedData="multipleSelection" 
        :isDelete="true" 
        :isMove="false" 
        tid="aeid" 
        :delApi="batchDeleteExecStatusHistory" 
        @result="isSuccess" 
        v-show="isShowBtnGroup"
        class="fl m-t-10 cf">
        </btn-group>

        <el-button 
        v-loading="clearLoading" 
        size="small" 
        @click="clearAll()" 
        type="primary" 
        class="fl m-t-10 cf m-l-10"
            v-show="isShowBtnGroup">
        {{$t('appSecurity.clearAll')}}
        </el-button>

        <div class="m-t-10 cf fr">
            <el-pagination
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :page-size="limit"
            :current-page="currentPage"
            :total="dataCount"
            v-show="isshow"
            background
            >
            </el-pagination>
            
        </div> 
        
       
    </div>
</template>
<script>
    import {reExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import{getExecStatusByTypeApi, 
    batchDeleteExecStatusHistoryApi, 
    deleteExecStatusHistoryApi,
    deleteExecStatusHistoryAllByTypeApi, 
    getExecStatusOverviewApi,
    getExecStatusByTypeAndDevIdApi, 
    getExecStatusByTypeAndGroupApi,
    deleteExecStatusHistoryAllApi,
    getExecStatusByPageAndTypeAndStatusInGroupApi,
    getExecStatusByPageAndTypeInGroupApi,
    deleteExecStatusHistoryAllByGroupAndTypeApi} from "@/components/restfulapi/execStatusApi"
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"
    import handleResponse from '@/components/restfulapi/handleResponse'
    import btnGroup from "../../../common/btn-group";
    import {singleInstallType, batchWlType} from "../../../assets/js/constant"
    import { androidControl } from '../../../assets/js/lwm2mMap';

export default {
    
    data() {
        return {
            execStatusHistoryTableList: [],
            isshow: false,
            limit: 10,
            dataCount: 0,
            currentPage: 1,
            multipleSelection: [],
            isShowBtnGroup: false,
            clearLoading: false,
            batchDeleteExecStatusHistory:  batchDeleteExecStatusHistoryApi,
            intervalTime: 3000,

            singleInstallType,
            batchWlType,
            listLoading: false,
            isInterval: false,
            //filter status
            statusArray: [],
        }
    },

    components: {
        btnGroup,
    },

    props: {
        statusType: {
            default: batchWlType,
            type: String
        },

        selectedAgentId: {
            default: "",
            type: String
        },

        groupname: {
            default: "",
            type: String
        }
    },
    methods: {
        selectItem(val) {
            this.multipleSelection = val;
        },

        checkSelectable(row) {
            return !row.status==0;
        },

        handleCurrentChange(currentPage){
            if(currentPage){
                this.currentPage = currentPage;
            }else{
                this.currentPage = 1
            }
            this.intervalGetExecStatusHistory();
            
        },

        deleteHistory(aeid){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.listLoading = true;
                    deleteExecStatusHistoryApi(aeid).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetExecStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            }) 
        },

        getExecStatusHistoryByGroupname(){
            if(!this.groupname){
                console.error("[getExecStatusHistoryByGroupname]groupname is empty")
            }
            if(this.isInterval === false){
                this.listLoading = true;
            }
            if(this.statusArray.length > 0){
                getExecStatusByPageAndTypeAndStatusInGroupApi(this.groupname, this.statusType, this.currentPage, this.limit, this.statusArray).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignExecStatusData(res); 
                    })
                })
             }else{
                 getExecStatusByPageAndTypeInGroupApi(this.groupname, this.statusType, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignExecStatusData(res);
                    })
                })
            }
        },

        assignExecStatusData(res){
            if(Object.prototype.toString.call(res) === "[object Object]" && Object.prototype.toString.call(res.data) === '[object Array]'){
                this.dataCount = res.count;
                this.execStatusHistoryTableList = res.data;
                this.isshow = this.dataCount > this.limit;
                this.isShowBtnGroup = this.dataCount > 0;
                this.$emit("getStatusCount")
                let isFinish = true;
                res.data.forEach((val) => {
                    if(val.status == 0){
                        isFinish = false;
                    }
                })
                if(isFinish){
                    this.$emit("isFinished");
                    window.clearInterval(_g.etimer);
                    _g.etimer = null;
                }
            }else{
                window.clearInterval(_g.etimer);
                _g.etimer = null;
            }
            
        },

        intervalGetExecStatusHistory(){
            this.getExecStatusHistoryByGroupname();
            if(_g.etimer === null){
                _g.etimer = window.setInterval(() => {
                    this.getExecStatusHistoryByGroupname();
                    this.isInterval = true;
                },this.intervalTime)
            }  
        },


        filterStatus(value){
            this.statusArray = value.status;
            this.currentPage = 1;
            if(_g.isArray(this.statusArray)){
                this.intervalGetExecStatusHistory();
            }
            
        },

        resetAppSensor(cid, selectedAgentId, execdata){
            if(androidControl[cid] === undefined){
                this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                return;
            }
            this.listLoading = true;
            reExecDeviceStatus(selectedAgentId, androidControl[cid], execdata).then((data) => {
                this.listLoading = false;
                handleResponse(data, (res) => {
                    if(res.status == "CHANGED"){
                        this.activeName = "execStatusHistory";
                        this.currentPage = 1;
                        this.intervalGetExecStatusHistory();
                    }else{
                        _g.handleError(res)
                    }
                })
                
            })
            
        },

        reexec(execItem){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('appSecurity.reexec'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    let execdata = {
                        taskname: execItem.taskname,
                        value: execItem.value,
                    }
                    this.resetAppSensor("whitelist", execItem.deviceId, execdata);
                    
                }
            })
        },

        //delete appstatus $emit
        isSuccess(result){
            if(result){
                this.intervalGetExecStatusHistory();
            }
        },

         //clear All history
        clearAll(){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.clearLoading = true;
                    deleteExecStatusHistoryAllByGroupAndTypeApi(this.groupname, this.statusType).then((data) => {
                        this.clearLoading = false;
                        handleResponse((data), (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                    this.intervalGetExecStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            })
        },

        initData(){
            this.execStatusHistoryTableList = [];
            this.isShowBtnGroup= false;
            this.isshow= false;
            this.currentPage = 1;
        },

        checkExecStatus(){
            getExecStatusByTypeAndGroupApi(this.groupname, this.statusType)
        }


    },

    created(){
        this.$emit("getStatusCount")
    },
    beforeDestroy() {
         window.clearInterval(_g.etimer);
        _g.etimer = null;
    },

    watch: {
        groupname(val){
            if(val){
                this.initData();
                this.checkExecStatus();
                this.intervalGetExecStatusHistory();
                
            }  
},
    
    },
        
}
</script>
<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        // background-color: #777;
        border-radius: 10px;
    }
    .el-loading-mask{
        top: 60px;
    }

    
</style>



